.comment {
  position: relative;
  /* margin-top: 15px; */
  margin-bottom: 15px;
  width: 100%;
  border: 1px solid rgb(216, 216, 216);
  border-radius: 5px;
}

.comment-title {
  background-color: rgb(241, 241, 241);
  padding: 5px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
}

.comment-body {
  padding: 5px;
  background-color: #fff;
}

.comment-text {
  padding-left: 15px;
  padding-right: 15px;
}



